<script>
	import uCharts from '@/components/u-charts/u-charts.js';
	var _self;
	var canvaRing=null;
export default {
	data() {
		return {
			tabs: ['律师服务', '律师信息', '律师案例'],
			active: 0,
			cWidth:'',
			cHeight:'',
			pixelRatio:1,
			
			"chartData": {
			  "series": [{
				"name": "房产纠纷",
				"data": 22
			  }, {
				"name": "知识产权",
				"data": 6
			  }, {
				"name": "合同纠纷",
				"data": 21
			  }, {
				"name": "婚姻家庭",
				"data": 6
			  }, {
				"name": "其他",
				"data": 45
			  }]
			}
		};
	},
	created() {},
	onLoad(option) {
		_self = this;
		this.cWidth=uni.upx2px(750);
		this.cHeight=uni.upx2px(500);
		this.getServerData();
	},
	methods: {
		navigateBack() {
			uni.navigateBack();
		},
		qiehuan(index) {
			this.active = index;
		},
		getServerData(){
		    _self.showRing("canvasRing",this.chartData);
		},
		showRing(canvasId,chartData){
			canvaRing=new uCharts({
				$this:_self,
				canvasId: canvasId,
				colors: ['#c23531', '#91c7ae', '#67afb8', '#d48265', '#3f5b6e'],
				type: 'ring',
				fontSize:11,
				legend:true,
				// title: {
				// 	name: '70%',
				// 	color: '#7cb5ec',
				// 	fontSize: 25*_self.pixelRatio,
				// 	offsetY:-20*_self.pixelRatio,
				// },
				// subtitle: {
				// 	name: '收益率',
				// 	color: '#666666',
				// 	fontSize: 15*_self.pixelRatio,
				// 	offsetY:30*_self.pixelRatio,
				// },
				extra: {
					pie: {
					  offsetAngle: -45,
					  ringWidth: 40*_self.pixelRatio,
					  labelWidth:15,
					}, 
				},
				background:'#FFFFFF',
				pixelRatio:_self.pixelRatio,
				series: chartData.series,
				animation: true,
				width: _self.cWidth*_self.pixelRatio,
				height: _self.cHeight*_self.pixelRatio,
				disablePieStroke: true,
				dataLabel: true,
			 
				   
			});
		},
		touchRing(e){
			canvaRing.showToolTip(e, {
				format: function (item) {
					return item.name + ':' + item.data 
				}
			});
		},
		// 主页提问详情
		go_tiwen_xq(){
			uni.navigateTo({
				url:'zhuye_zixun_xq'
			})
			
		},
		tuwen_xq(){
			uni.navigateTo({
				url:'tuwen_xq'
			})
		},
		go_dianping(){
			uni.navigateTo({
				url:'pingjia'
			})
		}
	}
};
</script>

